// ------------------------------------------------------------------------------------------
// import
// ------------------------------------------------------------------------------------------
import axios from 'axios'
import store from '../store/index.js'
import i18n from '@/i18n';

// ------------------------------------------------------------------------------------------
// Global function
// ------------------------------------------------------------------------------------------
const Ace = window.Ace = { PLUGIN_VERSION: '0.0.000000' }
let _primeDialogConfirm = null;
let _i18n = undefined;
axios.defaults.baseURL = store.state.apiUrl;

Ace.Install = function (vue) {
    
    _i18n = i18n.global;

    axios.interceptors.request.use((config) => {
        store.commit("changeBlockUI", true);

        return config
    }, (error) => {
        store.commit("changeBlockUI", false);
        if (error.response && error.response.data) {
            return Promise.reject(error.response.data)
        }
        return Promise.reject(error.message)
    })

    axios.interceptors.response.use((response) => {
        setTimeout(() => {
            store.commit("changeBlockUI", false);
        }, 10);
        const result = response.data
        if (response.status === 200) {
            if (typeof result === 'object' && result.data.token) {
                if (result.code == 0) {
                    localStorage.setItem('__gf_accessToken', result.data.token)
                    store.commit('setAccessToken', result.data.token)
                }
            }
            // -1 => 傳入參數遺漏, -2 => token fail, -3 => token expiry, -4 => api error, -5 => customize error
            else if ((result.code == -2) || (result.code == -3)) {
                Ace.alert(_i18n.t("_global.error." + result.data.id), _i18n.t("_global.confirmDialog.title.error"), "pi pi-info-circle", () => { Ace.logout() });
            }
            else if ((result.code == -1) || (result.code == -4)) {
                Ace.alert(_i18n.t("_global.error." + result.data.id), _i18n.t("_global.confirmDialog.title.error"), "pi pi-info-circle");
            }
            else if (result.code == -5) {
                Ace.alert(_i18n.t("_global.error." + result.data.id), _i18n.t("_global.confirmDialog.title.error"), "pi pi-info-circle");
            }
        }

        return result
    }, (error) => {
        store.commit("changeBlockUI", false);
        Ace.alert(_i18n.t("_global.confirmDialog.content.callApiError"), _i18n.t("_global.confirmDialog.title.warning"), "pi pi-info-circle");
        if (error.response && error.response.data) {
            return Promise.reject(error.response.data)
        }
        return Promise.reject(error.message)
    })

    _primeDialogConfirm = vue.config.globalProperties.$confirm;
}

Ace.alert = function (message, title, icon, callback) {
    _primeDialogConfirm.require({
        message: message,
        header: title,
        icon: icon,
        rejectClass: 'confirm-reject-hide',
        accept: callback
    });
}

Ace.logout = function () {
    if (store.state.accessToken !== undefined) {
        localStorage.removeItem('__gf_accessToken')
        window.location.href = "/"
    }
}

Ace.tableDataLan = function (columnName) {
    let name = "";
    switch (store.state.language) {
        case "zhTW":
            name = columnName + "C";
            break;
        case "enUS":
            name = columnName + "E";
            break;
    }
    return name;
}

Ace.getUserInfo = function () {
    axios.post('/api/User/GetUserInfo', { token: store.state.accessToken })
        .then((res) => {
            if (res.code == 0) {
                store.commit('setUserInfo', res.data.data);
            }
        })
        .catch((err) => {
            console.log("err:", err);
        })
}

Ace.checkTokenLogin = async function (methods = undefined, iGroup = 2) {
    console.log("Ace.checkTokenLogin_Api");
    if (store.state.accessToken != "") {
        if (Ace.checkPageIllegal(store.state.userInfo.group, iGroup)) {
            this.alert(_i18n.t("_global.confirmDialog.content.illegalAccess"), _i18n.t("_global.confirmDialog.title.warning"), "", () => { this.logout() });
            return;
        }
        else {
            if (methods !== undefined) {
                methods();
            }
        }
        return;
    }
    else if ((localStorage.getItem("__gf_accessToken") == null) || (localStorage.getItem("__gf_accessToken") == undefined)) {
        this.alert(_i18n.t("_global.confirmDialog.content.tokenIllegal"), _i18n.t("_global.confirmDialog.title.warning"), "", () => { this.logout() });
        return;
    }
    else {
        let sToken = localStorage.getItem("__gf_accessToken");
        store.commit('setAccessToken', sToken);

        console.log("Ace.checkTokenLogin_Axios");

        await axios.post("/api/User/LoginFromToken", { token: sToken }).then((res) => {
            let result = res.data;
            if (result.data == null) {
                this.alert(_i18n.t("_global.confirmDialog.content.tokenIllegal"), _i18n.t("_global.confirmDialog.title.warning"), "", () => { this.logout() });
                return;
            }
            else if (Ace.checkPageIllegal(Number(result.data.group), iGroup)) {
                this.alert(_i18n.t("_global.confirmDialog.content.illegalAccess"), _i18n.t("_global.confirmDialog.title.warning"), "", () => { this.logout() });
                return;
            }
            else {
                console.log("Ace.checkTokenLogin_Axios_OK");
                store.commit('setUserInfo', result.data);
                if (methods !== undefined) {
                    methods();
                }
            }
        })
            .catch((err) => {
                console.log("err:", err);
            });
    }

    console.log("Ace.checkTokenLogin_Out");
}

Ace.checkPageIllegal = function (iUserGroup, iPageGroup) {
    if (iUserGroup > iPageGroup) {
        return true;
    }
    else {
        return false;
    }
}

// ------------------------------------------------------------------------------------------
// 基本函數擴充功能
// ------------------------------------------------------------------------------------------
Date.prototype.addDays = function (days) {
    this.setTime(this.getTime() + days * 86400000)
    return this
}

Date.prototype.addSeconds = function (seconds) {
    this.setTime(this.getTime() + seconds * 1000)
    return this
}

Date.prototype.toISOLocalString = function () {
    return this.getFullYear() + '-' + (this.getMonth() + 1).padLeft('0', 2) + '-' + this.getDate().padLeft('0', 2) + 'T' + this.getHours().padLeft('0', 2) + ':' + this.getMinutes().padLeft('0', 2) + ':' + this.getSeconds().padLeft('0', 2)
}

Number.prototype.padLeft = function (ch, base) {
    const nr = this; const iLength = (base - String(nr).length) + 1
    return ((iLength > 0) ? new Array(iLength).join(ch) + nr : nr.toString())
}

Number.prototype.padRight = function (ch, base) {
    const nr = this; const iLength = (base - String(nr).length) + 1
    return ((iLength > 0) ? nr + (new Array(iLength).join(ch)) : nr.toString())
}

Number.prototype.toThousand = function (decimal) {
    const re = '\\d(?=(\\d{3})+' + (decimal > 0 ? '\\.' : '$') + ')'
    return this.toFixed(Math.max(0, Number(decimal))).replace(new RegExp(re, 'g'), '$&,')
}

String.prototype.padLeft = function (ch, base) {
    const nr = this; const iLength = (base - nr.length) + 1
    return ((iLength > 0) ? new Array(iLength).join(ch) + nr : nr)
}

String.prototype.toDate = function () {
    const iDates = this.split(/[^0-9]/)
    return new Date(iDates[0], iDates[1] - 1, iDates[2], iDates[3], iDates[4], iDates[5])
}

export { Ace };