import { createI18n } from 'vue-i18n'
import language_ZHTW from './languages/zh-TW.js'
import language_ENUS from './languages/en-US.js'

const i18n = createI18n({
  locale: localStorage.getItem("locale") ?? 'enUS',
  fallbackLocale: "enUS",
  messages: {
    'zhTW': language_ZHTW,
    'enUS': language_ENUS
  }
})

export default i18n
